@import url(https://fonts.googleapis.com/earlyaccess/notosansjp.css);

body {
    font-family: 'Noto Sans JP', sans-serif;
}

main {
    width: 75%;
    margin: auto;
}

a {
    color: #366ad3;
}

a:hover {
    color: #e53;
}

p {
    margin: 0;
}

p + p {
    margin-top: 1rem;
}

header {
    border-bottom: 1px solid #c0c0c0;
    background-color: white;
    margin-bottom: 30px;
    overflow: hidden;
}

.logo {
    width: 80%;
    max-width: 300px;
}

.aboutContainer {
    width: 75%;
    margin: 15px auto 20px auto;
}

.about {
    border-radius: 0.5rem;
    border: 2px solid #018f75;
}

.about + .about {
    margin-top: 10px;
}

.about > summary {
    display: block;
    padding: 0.3rem 1rem;
    cursor: pointer;
    user-select: none;
    background-color: white;
    border-radius: 0.5rem;
}

.about > summary::-webkit-details-marker {
    display: none;
}

.summaryIcon {
    font-weight: bold;
}

.about[open] > summary {
    border-radius: 0.5rem 0.5rem 0 0;
}

.about[open] .summaryIcon {
    transform: rotate(180deg);
}

.summaryInner{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.aboutContent {
    padding: 1rem;
    border-top: 1px dashed #018f75;
}

.logobox {
    text-align: center;
    margin: 20px 0;
}

.wordForm {
    margin-top: 10px;
}

.wordForm .formName {
    font-size: 16px;
    font-weight: bold;
    margin: 5px 0 3px 0;
}

.wordForm .formDesc {
    font-size: 14px;
}

.wordForm .inputForm {
    width: 100%;
    box-sizing: border-box;
    padding: 0.3rem 0.5rem;
    border: 1px solid #014f41;
    border-radius: 0.3rem;
}

.wordForm textarea {
    resize: vertical;
}

.wordForm .submit {
    cursor: pointer;
    border: none;
    background-color: #014f41;
    color: white;
    padding: 5px 8px;
    border-radius: 0.3rem;
    margin-top: 10px;
}

.search {
    display: flex;
    text-align: center;
    justify-content: center;
    margin: 15px 0;
}

.searchOption > div {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 15px 0;
}

.searchOption > div > label > span {
    display: inline-block;
	cursor: pointer;
    min-width: 6em;
	margin: 0;
	padding: 0.3rem;
	border-right: 1px solid #b0b0b0;
	background: #d7d7d7;
	color: black;
	font-size: 14px;
	text-align: center;
}

.searchOption > div > label > input {
    display: none;
}

.searchOption > div > label:first-child > span {
    border-radius: 0.3rem 0 0 0.3em;
}

.searchOption > div > label:last-child > span {
    border-right: none;
    border-radius: 0 0.3rem 0.3rem 0;
}

.searchOption > div > label > input:checked + span {
    background-color: #014f41;
    color: white;
}

#input {
    width: 80%;
    border: 2px solid #014f41;
    padding: 0.5rem;
    border-radius: 0.5rem;
}

.content {
    background-color: white;
    box-sizing: border-box;
    border: 1px solid #c0c0c0;
    width: 100%;
    margin: 15px auto;
    padding: 5px;
}

.word {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    margin: 0.8rem 0.8rem 0.5rem 0.8rem;
}

.word .subWord {
    margin: 5px 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.mean span {
    margin: 3px 5px;
}

.phun {
    font-size: 28px;
    font-family: "Phun-Sans", "PhunWrite", serif;
    margin: 0 15px 15px 0;
    padding: 0 5px;
    border: solid #202020;
    border-width: 0 5px;
    line-height: 1.1em;
}

.trans {
    font-weight: bold;
    font-size: 16px;
    margin-right: 15px;
}

.pron {
    font-size: 14px;
}

.num {
    font-size: 12px;
    flex-grow: 1;
    text-align: right;
    vertical-align: top;
    margin-bottom: auto;
}

.speech {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: start;
    text-align: left;
    margin-top: 0.5rem;
}

.speech:first-child {
    margin-top: 0;
}

.type {
    width: fit-content;
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    padding: 0 0.25rem;
    font-size: 14px;
    border: 1px solid #b0b0b0;
    border-radius: 0.3rem;
    min-width: 1.5rem;
    min-height: 1.5rem;
    text-wrap: nowrap;
}

.wordDesc {
    border-top: 1px dashed #a0a0a0;
    padding: 0.8rem;
}

.meanExp .type {
    font-weight: bold;
}

.expRow {
    display: flex;
    align-items: center;
}

.expNum {
    box-sizing: border-box;
    display: inline-block;
    border: 1px solid #303030;
    border-radius: 2px;
    font-weight: bold;
    line-height: 1.5em;
    min-height: 1.5em;
    min-width: 1.5em;
    padding: 0 0.25em;
    font-size: 12px;
    margin-right: 0.5rem;
}

.expTag {
    font-feature-settings: "palt";
    width: fit-content;
    display: inline-block;
    box-sizing: border-box;
    text-align: center;
    margin-right: 0.2rem;
    padding: 0.1rem;
    font-size: 0.8rem;
    line-height: 0.8rem;
    text-wrap: nowrap;
}

.text {
    line-height: 1.5rem;
    margin-left: 0.5rem;
    font-size: 16px;
    text-align: left;
    width: fit-content;
}

footer {
    background-color: white;
    font-size: 14px;
    border-top: 1px solid #c0c0c0;
    text-align: center;
    margin: 30px 0 10px 0;
    overflow: hidden;
}

footer div {
    margin: 10px 0;
}

@font-face {
    font-family: "PhunWrite";
    src: url(https://kaeru2193.github.io/Phun-Resources/font/PhunWrite-Regular-latest.woff) format("woff");
}

@font-face {
    font-family: "WeQo";
    src: url(https://kaeru2193.github.io/Phun-Resources/font/WeQoGothic.woff) format("woff");
}

@font-face {
    font-family: "Phun-Sans";
    src: url(https://kaeru2193.github.io/Phun-Resources/font/Phun-Sans/Phun-Sans-Rounded-Bold.woff) format("woff");
}

@media screen and (max-width: 834px) {
    .searchOption {
        display: flex;
        justify-content: space-around;
        align-items: start;
    }
    .searchOption > div {
        flex-direction: column;
    }

    .searchOption > div > label > span {
        width: 6em;
        border-right: none;
        border-bottom:  1px solid #a0a0a0;
    }

    .searchOption > div > label:first-child > span {
        border-radius: 0.3rem 0.3rem 0 0;
    }
    
    .searchOption > div > label:last-child > span {
        border-bottom: none;
        border-radius: 0 0 0.3rem 0.3rem;
    }
}

@media screen and (max-width: 600px) {
    .speech{
        flex-direction: column;
    }
    .type {
        margin: 5px 0;
    }
}